import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom'; // useNavigate  
import { useAuth } from '../../contexts/AuthContext';
import { Label } from "./ui/label"
import { Input } from "./ui/input"
import { Button } from "./ui/button"
import axios from 'axios';

function LoginPage() {
	const [username, setUsername] = useState('');
	const [password, setPassword] = useState('');
	const { login } = useAuth();
	const navigate = useNavigate();


	useEffect(() => {
		const queryParams = new URLSearchParams(window.location.search);
		const accesstoken = queryParams.get('accesstoken');
		const nickname = queryParams.get('nickname');

		if (accesstoken) {
			localStorage.setItem('userInfo', accesstoken);
			localStorage.setItem('nickname', nickname);
			navigate('/index');
		}
	}, []);

	const handleLogin = async () => {
		try {
			const params = new URLSearchParams();
			params.append('username', username);
			params.append('password', password);


			const response = await axios.post('http://localhost:8080/api/Login', params, {
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				withCredentials: true
			});
			console.log('asd');
			localStorage.setItem('userInfo', response.data.accessToken);
			localStorage.setItem('nickname', response.data.nickname);
			await login(username, password); //   	    
			alert(' ');
			navigate('/index');
		} catch (error) {
			if (error.response) {
				const status = error.response.status;
				if (status === 401 || status === 403) {
					alert('  .');
				} else {
					alert('   .  .');
				}
			} else {
				alert('   .');
			}
		}
	};

	const test = async () => {
		try {
			const params = new URLSearchParams();
			params.append('username', username);
			params.append('password', password);


			const response = await axios.post('http://13.125.161.122:8080/api/Login', params, {
				//const response = await axios.post('http://localhost:8080/api/Login', params, {
				//const response = await axios.post('http://192.168.200.143:8080/api/Login', params, {
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				withCredentials: true
			});
			console.log('');
			localStorage.setItem('userInfo', response.data.accessToken);
			localStorage.setItem('nickname', response.data.nickname);
			await login(username, password); //   	    
			alert(' ');
			navigate('/index');
		} catch (error) {
			if (error.response) {
				const status = error.response.status;
				if (status === 401 || status === 403) {
					alert('  .');
				} else {
					alert('   .  .');
				}
			} else {
				alert('   .');
			}
		}
	};

	const handleSubmit = async (e) => {
		e.preventDefault(); //    
		await handleLogin(); //  
	};


	return (
		<div className="flex flex-col gap-6 p-4 sm:p-6 items-center justify-center min-h-screen">
			<div className="w-full max-w-[400px]">

				<div className="text-center py-6">
					<h1 className="text-3xl font-bold">SNS</h1>
					<p className="text-gray-500 dark:text-gray-400">!</p>
				</div>

				<form onSubmit={handleSubmit}>
					<div className="space-y-4">
						<div className="space-y-2">
							<Label htmlFor="username"></Label>
							<Input id="username" placeholder="r___k18" value={username} onChange={(e) => setUsername(e.target.value)} />
						</div>
						<div className="space-y-2">
							<Label htmlFor="password"></Label>
							<Input id="password" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
						</div>
						<div className="mt-4 space-y-2 text-center">
						</div>
						<div style={{ textAlign: 'center' }} >
							<Link className="text-sm underline" to="#">
								 
							</Link>
							<span style={{ margin: '0 10px' }}>|</span>{" "}
							<Link className="text-sm underline" to="/Signup"></Link>
						</div>
						<Button type="submit" className="w-full mb-2" variant="outline" style={{ backgroundColor: 'black', borderColor: 'black', borderWidth: '2px', color: 'white' }}></Button>
					</div>
				</form>
				<br />

				<div className="space-y-4">
					<Button className="w-full" style={{ backgroundColor: '#03C75A' }} variant="outline"
						onClick={test} >
						ip 
					</Button>
					<Button className="w-full" style={{ backgroundColor: '#03C75A' }} variant="outline"
						onClick={() => window.location.href = 'http://localhost:8080/oauth2/authorization/naver'} >
						NAVER
					</Button>
					<Button className="w-full" variant="outline"
						onClick={() => window.location.href = 'http://localhost:8080/oauth2/authorization/google'} >
						GOOGLE
					</Button>

					<Button className="w-full" style={{ backgroundColor: '#FEE500' }} variant="outline"
						onClick={() => window.location.href = 'http://localhost:8080/oauth2/authorization/kakao'} >
						KAKAO
					</Button>
				</div>
			</div>
		</div>
	);
}

export default LoginPage;