/*BoardWrite*/

import { useNavigate, useLocation } from 'react-router-dom'; // useNavigate  
import styled from 'styled-components'; // styled-components import 
import { Button } from "./ui/button"
import api from "../../api";
import { CardTitle, CardHeader, CardContent, CardFooter, Card } from "./ui/card"
import React, { useState, useEffect } from 'react';
//npm install styled-components !

const StyledTextarea = styled.textarea`
  /*   */
  border: 1px solid #ccc; /*    */
  padding: 8px; /*   */
  font-size: 16px; /*   */
  border-radius: 8px;
  resize: none;
  width: 100%;
  height: 200px;
  overflow-y: auto;
  /*      */
`;


function BoardWrite() {
	const navigate = useNavigate();
	const location = useLocation();
	const [content, setContent] = useState('');
	const [images, setImages] = useState([]);
	const fileInputRef = React.useRef(null);
	const fromPath = location.state?.from || '/';  //     
	/*const post = location.state.post;*/
	const [post] = useState(location.state?.post || null);

	useEffect(() => {
		console.log(post);
		if (post) {
			setContent(post.content || '');
		} else {
			setContent('');
		}
	}, [post]);

	//     
	const handleFileChange = (event) => {
		setImages([...event.target.files]);
	};

	//      ()
	const displayFileInfo = () => {
		if (images.length > 0) {
			return images.map((file, index) => (
				<div key={index}>
					<p> : {file.name}</p>
				</div>
			));
		} else {
			return /*<p>  .</p>*/;
		}
	};
	//      
	const handleContentChange = (event) => {
		setContent(event.target.value); //      .
	};



	// ''   
	const Write = async () => {
		const formData = new FormData(); // FormData  
		
		formData.append('content', content); //   
		formData.append('nickname', localStorage.getItem("nickname"));
		images.forEach((image) => {
			formData.append('img', image);
		});
		try {
			if (post) {
				//  
				formData.append('board_id', post.board_id);
				formData.append('imgpath', post.imgpath);
				await api.post(`/boardUpdate`, formData, {
					headers: {
						'Content-Type': 'multipart/form-data',
					},
					withCredentials: true,
				});
				alert(' .');
			} else {
				//    
				await api.post('/boardWrite', formData, {
					headers: {
						'Content-Type': 'multipart/form-data',
					},
					withCredentials: true,
				});
				alert('  .');
			}
			navigate(fromPath);
		} catch (error) {
			console.log(error);
			alert(' .  .');
		}
	};
	const handleButtonClick = () => {
		fileInputRef.current.click();
	};


	return (
		<Card className="w-[75vw] max-w-sm mx-auto">
			<CardHeader>
				<CardTitle className="text-xl">{post ? ' ' : '  '}</CardTitle>
			</CardHeader>
			<CardContent className="grid gap-4">
				<StyledTextarea className="flex-1" placeholder="  ." value={content} onChange={handleContentChange} />
				<div className="flex flex-col gap-2">
					<input type="file" name="img" id="img" ref={fileInputRef} onChange={handleFileChange} multiple style={{ display: 'none' }} />
					<Button size="sm" onClick={handleButtonClick}>
						<UploadIcon className="h-4 w-4 mr-2" />
						Add images
					</Button>
					<Button size="sm">
						<UploadIcon className="h-4 w-4 mr-2" />
						Add videos
					</Button>
					{post ? (
						<>
							{post.imgpath && (
								<div>
									<p> :</p>
									<img src={post.imgpath} alt=" " />
								</div>
							)}
							{post.video && <video src={post.video} controls />}
							{!post.img && displayFileInfo()}
						</>
					) : (
						displayFileInfo()
					)}
				</div>
				<div className="grid gap-4">
					<Card className="rounded-none shadow-none border-0">
					</Card>
				</div>
			</CardContent>
			<CardFooter>
				<Button onClick={Write}>{post ? '' : ''}</Button>
			</CardFooter>
		</Card>
	);
}

function UploadIcon(props) {
	return (
		<svg
			{...props}
			xmlns="http://www.w3.org/2000/svg"
			width="24"
			height="24"
			viewBox="0 0 24 24"
			fill="none"
			stroke="currentColor"
			strokeWidth="2"
			strokeLinecap="round"
			strokeLinejoin="round">
			<path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
			<polyline points="17 8 12 3 7 8" />
			<line x1="12" x2="12" y1="3" y2="15" />
		</svg>
	)
}

export default BoardWrite;
